import React from "react";
import Slogan from "./UI/Slogan";

const TextbookPreview = () => {
  return (
    <div className="third_container">
      <div className="second-content">
        <div
          className="slogan"
          style={{ gap: "34px" }}
        >
          <Slogan
            main={"   "}
            bottom={
              "         "
            }
          />
        </div>
        <img
          className="pict"
          src="images/study-girl.svg"
          alt="girl"
        />
      </div>
    </div>
  );
};

export default TextbookPreview;
